  margin: 0 0 0 1.8em;
  padding: 2px;
  border-radius: 11px;
}

input.toggle + .indicator::before {
  display: inline-block;
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 8px;
  background-color: var(--input-toggle-off-color);
  margin: 0;
  padding: 0;
  left: 3px;
  top: 2px;
  transition: all 250ms;
}

input.toggle:checked + .indicator::before {
